import React, { useCallback, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { Container, Typography, Paper, Box, CircularProgress, Alert } from '@mui/material';

const Upload = () => {
  const [uploading, setUploading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);

  const onDrop = useCallback(async (acceptedFiles) => {
    const file = acceptedFiles[0];
    if (!file) return;

    setUploading(true);
    setError(null);
    setSuccess(false);

    const formData = new FormData();
    formData.append('file', file);

    try {
      const response = await fetch('/api/v1/upload', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.detail || 'Upload failed');
      }

      const data = await response.json();
      setSuccess(true);
      console.log('Upload successful:', data);
    } catch (err) {
      setError(err.message);
      console.error('Upload error:', err);
    } finally {
      setUploading(false);
    }
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'application/pdf': ['.pdf']
    },
    multiple: false
  });

  return (
    <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h4" gutterBottom>
        Upload Documents
      </Typography>
      <Paper
        {...getRootProps()}
        sx={{
          p: 4,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          cursor: 'pointer',
          backgroundColor: isDragActive ? '#f0f9ff' : '#ffffff',
          border: '2px dashed #1976d2',
          '&:hover': {
            backgroundColor: '#f0f9ff'
          }
        }}
      >
        <input {...getInputProps()} />
        <Box sx={{ textAlign: 'center' }}>
          <Typography variant="h6" gutterBottom>
            {isDragActive ? 'Drop the files here' : 'Drag and drop files here'}
          </Typography>
          <Typography color="textSecondary">
            or click to select files
          </Typography>
          <Typography variant="body2" color="textSecondary" sx={{ mt: 2 }}>
            Supported formats: PDF
          </Typography>
        </Box>
      </Paper>

      {uploading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
          <CircularProgress />
        </Box>
      )}

      {error && (
        <Alert severity="error" sx={{ mt: 2 }}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mt: 2 }}>
          File uploaded successfully! You can now view the analysis.
        </Alert>
      )}
    </Container>
  );
};

export default Upload;